function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`.slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,

            navigation: {
                nextEl: `.slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function gallery() {
    let SlidersArray = []

    let sliders = document.querySelectorAll(".gallery")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        let gallery = {
            mySwiper: {},
            mySwiper2: {}
        }

        gallery.mySwiper = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper`, {
            spaceBetween: 0,
            slidesPerView: "auto",
            freeMode: true,
            watchSlidesProgress: true,
            direction: "vertical",
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });
        gallery.mySwiper2 = new Swiper(`.gallery[data-slider-id="${index}"] .mySwiper2`, {
            spaceBetween: 0,
            direction: "vertical",
            loop: true,
            navigation: {
                nextEl: `.gallery[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.gallery[data-slider-id="${index}"] .swiper-button-prev`,
            },
            thumbs: {
                swiper:  gallery.mySwiper,
            },
            breakpoints: {
                320: {
                    direction: "horizontal",
                },
                992: {
                    direction: "vertical",
                }
            }
        });

        SlidersArray[index] = gallery
    })

}

const sliderDelay = 5000
main = document.querySelector(".main")

function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        speed: 1000,
        autoplay: {
            delay: sliderDelay,
            disableOnInteraction: false,
        },
        navigation: {
            nextEl: `.main .swiper-button-next`,
            prevEl: `.main .swiper-button-prev`,
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
            renderBullet: function(index, className) {
                return `
                    <div class="swiper-pagination-bullet ${className}"><span></span></div>
                `;
            },
        },
    })
    swiper.on('slideChangeTransitionEnd', function() {
        let paginationItems = document.querySelectorAll(".main .swiper-pagination-bullet span")
        paginationItems.forEach(el => {
            el.style.width = "0%";
        })
        setTimer(sliderDelay)
    });
}
mainSlider()


if (main) {
    setTimer(sliderDelay)
}

function setTimer(delay) {
    let activePagination = document.querySelector(".main .swiper-pagination-bullet-active span")
    let progress = 1
    let interval = setInterval(function() {
        activePagination.style.width = `${progress+1}%`
        progress++
        if (progress === 99) {
            stopTimer()
        }
    }, (delay / 100));
    function stopTimer() {
        clearInterval(interval);
        clearProgress()
    }
    function clearProgress() {
        let paginationItems = document.querySelectorAll(".main .swiper-pagination-bullet span")
        paginationItems.forEach(el => {
            el.style.width = "0%";
        })
    }
}




Sliders()
gallery()
